import interfaceurl from '@/utils/interface.js'
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	computed: mapState(['userInfo']),
	data() {
		return {
			orderList: [], //订单列表
			orderParams: {
				userId: 0,
				startDT: '',
				endDT: '',
				start: 0,
				length: 10,
				status: '' //0:待支付1:待发货2:已发货3:已完成
			}, //订单参数
		}
	},
	onLoad() {
	},
	onShow() {
		this.$store.commit("setCartOrder", []);
		this.getOrders();
	},
	methods: {
		confirmReceipt(orderId) {
			let confirmParams = {
				id: orderId,
				userId: this.userInfo.userId
			}
			interfaceurl.checkAuth(interfaceurl.confirmfinish, confirmParams).then((res) => {
				this.getOrders();
				uni.showToast({
				    title: '确认收货成功',
				    icon: 'none',
				    duration: 2000
				});
			});
		},
		toEvaluate(item) {
			//整个订单商品或单个评价
			this.$store.commit('setEvaluateGoods', item.products)
			this.$turnPage('evaluate', 'navigateTo')
		},
		gotoPay(item) {
			let that = this;
			let payParams = {
				userId: that.userInfo.userId,
				orderId: item.id,
				orderNo: item.orderNo
			}
			interfaceurl.checkAuth(interfaceurl.pay, payParams).then((res) => {
				uni.requestPayment({
				    timeStamp: res.data.timeStamp,
				    nonceStr: res.data.nonceStr,
				    package: res.data.package,
				    signType: res.data.signType,
				    paySign: res.data.paySign,
				    success: (res) => {
				        that.getOrders();
				    },
				    fail: (res) => {
						uni.showToast({
						    title: '支付失败',
						    icon: 'none',
						    duration: 2000
						});
				    },
				    complete: () => {
						
				    }
				});
			});
		},
		cancel(item) {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.orderDelete, {id: item.id}).then((res) => {
				this.getOrders();
				uni.showToast({
				    title: '订单取消成功',
				    icon: 'none',
				    duration: 2000
				});
			});
		},
		setStatus(val) {
			this.orderParams.status = val;
			this.getOrders();
		},
		getOrders() {
			let that = this;
			that.orderParams.userId = that.userInfo.userId;
			interfaceurl.checkAuth(interfaceurl.orderManage, that.orderParams).then((res) => {
				that.orderList = res.data;
				//计算总价，状态数字文字转换
				for(var item of that.orderList) {
					item.statusText = that.getStatusText(item.status);
				}
			});
		},
		getStatusText(status) {
			let text = '';
			switch (status){
				case 0: text = '待付款'
					break;
				case 1: text = '待发货'
					break;
				case 2: text = '已发货'
					break;
				case 3: text = '已完成'
					break;
				default:
					break;
			}
			return text;
		}
	}
}
